<template>
  <div class="app-container">
    <div class="login-box">
      <van-form ref="loginForm" @submit="handleLogin" :show-error-message="false">
        <van-row type="flex" justify="center" class="border-line">
          <van-field
            class="input-box"
            v-model="username"
            placeholder="请输入手机号码"
            :rules="[{ required: true, message: '请填写用户名' }]"
          />
        </van-row>
        <van-row type="flex" justify="center" class="border-line">
          <van-col span="10">
            <van-field
              class="input-box"
              v-model="password"
              placeholder="请输入验证码"
              :rules="[{ required: true, message: '请填写验证码' }]"
            />
          </van-col>
          <van-col span="14">
            <van-button
              class="btn-box"
              native-type="button"
              type="default"
              :disabled="second!=120"
              @click="verificationCode"
            >{{secondStr}}</van-button>
          </van-col>
        </van-row>

        <div style="margin: 32px 0;">
          <van-button round block class="submit" native-type="submit">确认登录</van-button>
        </div>
      </van-form>
    </div>
    <van-overlay :show="loading" @click="show = false">
      <div class="wrapper">
        <van-loading class="block" size="50px" vertical type="spinner">加载中...</van-loading>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { smsCode } from "@/api/login";
export default {
  name: "home",
  data() {
    return {
      timer: null,
      second: 120,
      loading: false,
      secondStr: "获取验证码",
      username: "",
      password: ""
    };
  },
  methods: {
    verificationCode() {
      smsCode(this.username).then(res => {
        if (res.code == 200) {
          this.timer = setInterval(() => {
            this.second--;
            if (this.second <= 0) {
              clearInterval(this.timer);
              this.secondStr = "获取验证码";
              this.second = 120;
            } else {
              
              this.secondStr = this.second + "秒后重新发送";
            }
          }, 1000);
        }
      });
    },
    handleLogin() {
      let values = {
        username: this.username,
        password: this.password
      };
      this.loading = true;
      this.$store
        .dispatch("Login", values)
        .then(res => {
          this.loading = false;
          if (res.code == "200") {
            this.$router.push({ path: "/" });
          } else {
            this.$message.error(res.msg);
          }
        })
        .catch(() => {
          this.loading = false;
        });
    }
  }
};
</script>
<style lang="less" scoped>
.app-container {
  background-image: url("../../assets/image/login-bg.jpg");
  background-repeat: no-repeat;
  background-color: #4392f9;
  background-size: 100%;
  width: 100%;
  box-sizing: border-box;
  height: 100vh;
  display: flex;
  padding: 0 10%;
  display: -webkit-flex;
  align-items: center;
  /deep/ .van-field__control {
    color: #fff;
  }
  .login-box {
    padding-top: 100px;
    margin: 5px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    .border-line {
      margin-bottom: 20px;
      border-bottom: 1px solid #fff;
    }
    .btn-box {
      margin: 10px 0;
      height: 30px;
      line-height: 30px;
      border-radius: 10px;
    }
    .submit {
      font-size: 20px;
      background: #fff;
      color: #2269d4;
    }
    .input-box {
      background: transparent;
    }
  }
}
</style>



